import Input from './Input'
import List from './List'
import React, { Component } from 'react'


//组件名首字母一定要大写
class TodoList extends Component {
    constructor() {
        super();
        this.state = {
            inpValue: '',
            todos:[
                {title:'1',done:false},
                {title:'2',done:true},
                {title:'3',done:true},
                {title:'4',done:false}
            ]
        }
    }

    handleChange = (e) => {
        //console.log(e.target.value);
        this.setState({ inpValue: e.target.value })
        
    }
    componentDidMount() {
        let todos = localStorage.getItem('todos');
        if (todos) {
            this.setState({
                todos: JSON.parse(todos)
            })
        }
    }
  
    componentDidUpdate() {
        localStorage.setItem(
            'todos',
            JSON.stringify(this.state.todos)
        )
        return true
    }
 
    addIndo = () => {
        if (this.state.inpValue.slice(0,1) == " ") {
            alert('输入为空');
            return;
        }
        this.setState({
            todos: [
                {
                    title: this.state.inpValue,
                    done: false
                },
                ...this.state.todos
            ]
        })
        this.state.inpValue = " ";
    }

    delIndo = (idx) => {
        this.setState({
            todos: this.state.todos.filter((item,index) => index != idx)
        },()=>{
            localStorage.setItem(
                'todos',
                JSON.stringify(this.state.todos)
            )
        });
    }

    toggle = (index) => {
        let newTodo = JSON.parse(JSON.stringify(this.state.todos));
        newTodo[index].done = !newTodo[index].done;
        this.setState({
            todos: newTodo
        })
    }

    renderTodos = (done) => {
        return this.state.todos.map((todo,idx) => {
            if(done == todo.done){
                return <li>
                    <input 
                        onClick = {()=>this.toggle(idx)}
                        checked={done} 
                        type='checkbox'
                    />
                    <span dangerouslySetInnerHTML={{__html:todo.title}}></span>
                    <button onClick={() => this.delIndo(idx)}> 删除 </button>
                </li>
            }
        })
    }
    
    render() {
        return (
            <div className="tdl1">
                <Input addIndo = {this.addIndo} handleChange = {this.handleChange}/>
                <List todos = {this.state.todos} renderTodos = {this.renderTodos}/>
            </div>
        )
    }
}
export default TodoList;