import React, { Component } from 'react'

export default class Input extends Component {
    constructor(props){
        super();
    }
    render() {
        return (
            <div>
                <label htmlFor="inp"> Todolist </label>  
                <input
                value = {this.props.inpValue} type='text' onChange = {(e) => this.props.handleChange(e)}/>
                <button onClick={() => this.props.addIndo()}> add </button>
            </div>
        )
    }
}


